
*{margin:0;padding:0} 
body{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background: rgb(15, 19, 37);
    font-weight: 400;
    color: #fff;
}

canvas{display: block}
#container{
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: -1;
    top: 0;
    left: 0;
    }
    
.content{
    display: inline-flex;
    flex-direction: row;
    gap: 100px;
    padding: 200px;
}

/* item */
.item{
    text-decoration: none;
    color: #eee;
}
.item a{
    text-decoration: none;
    color: #eee;
}
.item h2{
    font-weight: 100;
    color: #fff;
}
.item p{
    font-weight: 100;
    color: #ccc;
}

.item:last-child{
    margin-right: 300px;
}

img{
    width: 300px;
    height: 300px;
    opacity: 0.1;
}


